.about_us_page{
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
}

.about_inner{
    width: 100%;
    background-color: #A9C1A9;
    padding-top: 220px;
    padding-bottom: 210px;
    padding-left: 75px;
    padding-right: 75px;

    font-family: 'BPG Nino Mtavruli';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;

    display: flex;
    gap: 150px;

    color: #171F17;
}

.about_inner_left{
    width: 50%;
}

.about_inner_left_texts{
    margin-top: 55px;
    /* width: 560px; */
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.engTwins{
    font-size: 40px !important;
    font-family: 'Roboto' !important;
    color: red !important;
}

.about_us_list{
    list-style:inside;
    font-weight: bold;
}

.about_inner_left_texts ul{
    margin-left: 10px;
}

.about_inner_right{
    width: 50%;
    display: flex;
}

.aboutImage{
    width: 80%;
}

.aboutImage img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 30px;
    box-shadow: 0px 0px 25px 10px rgba(2, 159, 160, 0.5);
}

